@font-face {
  font-family: SoraLight;
  src: url("./fonts/Sora-Light.ttf");
  font-display: swap;
}

body {
  margin: 0;
  font-family: SoraLight, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #3090f7;
}

.website {
  min-height: 100vh;
  width: 100vw;
  padding-bottom: 50px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  background-attachment: fixed;
}

.logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.header {
  width: calc(100% - 20px);
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header-link {
  margin: 10px;
}

.header-brand {
  margin-bottom: 0px
}

.section {
  min-height: calc(100vh - 400px);
  margin-left: 50px;
  margin-right: 50px;
  padding: 20px;
  border-radius: 10px;
}

@media (max-width: 700px) {
  .section {
    margin-left: 25px;
    margin-right: 25px;
  }
}

.section-header {
  margin-bottom: 30px;
  text-align: center;
}

.about-me-left {
  margin-right: 50px;
  margin-left: 50px;
  margin-bottom: 30px;
  width: 200px;
}

.my-picture {
  object-fit: contain;
  object-position: top;
  margin-bottom: 20px;
  border-radius: 10px;
}

.about-me-links a {
  margin: 10px;
}

.about-me-links {
  display: flex; 
  flex-direction: row;
  flex-wrap: wrap;
}

.my-description {
  min-width: 50px;
  max-width: 500px;
  margin-right: 20px;
}

.about-me {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.projectButton {
  margin-right: 10px;
}

.projectButtons {
  display: flex;
  flex-direction: row;
}

.experience-subtitle {
  margin-top: 10px;
  margin-bottom: 20px;
}

.new-line {
  white-space: pre-line;
}

.experience-links {
  margin-bottom: 1rem;
}

.experience-links a{
  margin-right: 10px;
}